@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  overflow: hidden;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.layout {
  @apply flex w-screen h-screen flex-col overflow-hidden content-start;
}
aside {
  @apply flex flex-1 max-w-[332px] h-full p-4;
}
aside > div {
  @apply shadow border border-slate-100;
}
.asd {
  height: calc(100% - 5rem);
}
main {
  @apply flex flex-col flex-1 overflow-y-scroll my-4 px-4 bg-slate-50/50 shadow;
}
#mainLayout main > div {
  @apply flex justify-center gap-6 content-start flex-wrap py-6 relative;
}
#authLayout main > div {
  @apply flex flex-col justify-center flex-1 items-center;
}
#header {
  background: rgb(93, 113, 45);
  background: linear-gradient(
    90deg,
    rgba(93, 113, 45, 1) 0%,
    rgba(146, 192, 122, 1) 100%
  );
  @apply h-20 flex w-full py-2 px-8 items-center justify-between;
}

.notification {
  @apply flex w-64 items-center  shadow-md border p-2 text-slate-50 mb-2;
}
